<template>
  <div class="hea">
    <div class="hea-inner">
      <router-link :to="$public+'/'" class="df-vue">
        <img src="https://www.vue-js.com/public/images/vue.png" alt />
        <span>Vue.js</span>
      </router-link>
      <p class="creat"></p>
      <router-link :to="$public+'/topics/creat'" v-if="userInfo">
        <div class="age"></div>
      </router-link>
      <p class="creat"></p>
      <router-link :to="$public+'/my/messages'" v-if="userInfo">
        <div class="age"></div>
      </router-link>
      <p class="creat">VUE2.0</p>
      <p class="creat">API</p>
      <p class="creat"></p>
      <p class="creat"></p>
      <div v-if="!userInfo" class="login">
        <input type="text" v-model="text" />

        <el-button type="warning" @click="login" round></el-button>
      </div>
      <div v-else class="df-vue">
        <img :src="userInfo.avatar_url" alt />

        <el-button type="warning" @click="logRome" round></el-button>
      </div>
    </div>
  </div>
</template>

<script>
//         1cookie    2localStorage     3sessionStorage 
//localsession   local       session
//  
//  localStorage.setItem('''')
//  localStorage.getItem('')
//  localStorage.removeItem("")   localStorage.clear()   
//   number  string   boolean
//   f12App...
import axios from "axios";
export default {
  name: "hea",
  data() {
    return {
      text: "125c72b1-2803-41cb-8c79-a5372aff04f3",
      userInfo: null
    };
  },
  created() {
    if (sessionStorage.getItem("token")) {
      axios
        .post("https://www.vue-js.com/api/v1/accesstoken", {
          accesstoken: sessionStorage.getItem("token")
        })
        .then(res => {
          this.userInfo = res.data;
          sessionStorage.setItem("token", this.text);
        });
    }
  },
  methods: {
    login() {
      axios
        .post("https://www.vue-js.com/api/v1/accesstoken", {
          accesstoken: this.text
        })
        .then(res => {
          this.userInfo = res.data;
          sessionStorage.setItem("token", this.text);
          sessionStorage.setItem("user_id", res.data.id);
        });
    },
    logRome() {
      this.userInfo = null;
      sessionStorage.clear();
    }
  }
};
</script>

<style>
.age {
  color: rgb(30, 89, 199);
  font-size: 14px;
  cursor: pointer;
}
.hea-inner {
  background: rgb(6, 75, 4);
  width: 80%;
  margin: 0 auto;
  color: #fff;
  font-size: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.creat {
  font-size: 14px;
  cursor: pointer;
  color: #fff;
}
.hea {
  background-color: rgb(6, 75, 4);
  margin-bottom: 20px;
}
.hea-inner img {
  width: 50px;
  height: 50px;
}
.hea-inner span {
  color: #fff;
}
.df-vue {
  display: flex;
  align-items: center;
  padding: 6px 0;
}
</style>
